import {useState} from 'react';
import './App.css'
import AllProps from './Components/Frontend/Routes/AllProps'
import { BrowserRouter as Router } from 'react-router-dom'
import data from './Components/Backend/Data/Data'
import useToken from './Components/Backend/Hooks/Hooks';

const App = () => {
  const {productItems} = data
  const [cartItems, setCarItems] = useState([])

  const [searchName, setSearchName] = useState('');
  const [search, setSearch] = useState(productItems)

  /* ! Login state */
const [, setToken] = useToken()
const [name, setName] = useState()
const [email, setEmail] = useState()
const [pass, setPass] = useState()
const [phone, setPhone] = useState()

function handleLogin(e){
  window.localStorage.setItem('name', JSON.stringify(name))
  window.localStorage.setItem('phone', JSON.stringify(phone))
  window.localStorage.setItem('email', JSON.stringify(email))
  window.localStorage.setItem('pass', JSON.stringify(pass))

  e.preventDefault()
  setToken(JSON.stringify({
    name: name,
    phone: phone,
    email: email,
    password: pass
  }))
}


function handleToken(){
window.location.reload()
window.localStorage.removeItem('token')
}
/* ================== */

  const handleAddProduct = (product) =>{
    const productId = cartItems.find((item) => item.id === product.id)
    const checked = cartItems.every((item) => {return item.id !== product.id})
      if(checked){
          if(productId){
            setCarItems(cartItems.map((item) =>item.id === product.id ? {...productId, quantity: productId.quantity + 1}: item))
          }else{
            setCarItems([...cartItems, {...product, quantity: 1}])
          }
      }
  }
  const FilterProduct = (e) =>{
    e.preventDefault()
     const inputValue = e.target.value

     if(inputValue !== ""){
       const result = productItems.filter((item) =>{
         return item.title.toLowerCase().startsWith(inputValue.toLowerCase())
       })
       setSearch(result)
     }else{
       setSearch(productItems)
     }

     setSearchName(inputValue)
  }

 
  
  const handleRemoveProduct = (product) =>{
    const productId = cartItems.find((item) => item.id === product.id)

    if(productId.quantity === 1){
      setCarItems(cartItems.filter((item) =>item.id !== product.id))
    }else{
      setCarItems(cartItems.map((item) => item.id === product.id ? {productId, quantity: productId.quantity - 1} : item))
    }
  }
  return (
    <>
    <Router>
      <AllProps productItems={productItems} cartItems={cartItems} handleAddProduct={handleAddProduct}
       handleToken={handleToken} handleLogin={handleLogin} name={name} phone={phone} email={email} pass={pass}  setName={setName} setPhone={setPhone} setEmail={setEmail} setPass={setPass} handleRemoveProduct={handleRemoveProduct}
       searchName={searchName} FilterProduct={FilterProduct} search={search}
      />
    </Router>
    </>
  );
}

export default App;
