import {Link} from 'react-router-dom'
import './Home.css'
import Header from '../Header/Header'
import Footer from '../Footer/Footer'


const Home = ({productItems, cartItems, handleAddProduct, name, phone, email, handleToken, searchName, FilterProduct, search}) => {

    return (
        
           
        <>
        <Header cartItems={cartItems} name={name} email={email} handleToken={handleToken} phone={phone} searchName={searchName} FilterProduct={FilterProduct} />
             <section className="home">
                <div className="container">
                <ul className="product-list">
                {search && search.length > 0 ? (
                    search.map((item) =>(
                        <li key={item.id}>
                        <Link  className='product-link' to={`/card/${item.id}`}>
                            <div className="img-box">
                            <img className='shoes-img' src={item.image} alt="product-img" width='270' height='160' />
                            </div>
                            <div className="info-box">
                            <h3 className='product-title'>{item.title}</h3>
                                <h4 className='product-price'>{`$ ${item.price}`}</h4>
                            </div>
                        </Link>
                        <div className="btn-box">
                        <button onClick={() =>{
                             handleAddProduct(item)
                        }}className='add' >Add to Cart</button>
                        </div>
                    </li>
                    ))
                    ):(
                        <h1 className='nocarts'>No results found! <i className='nobox bx bx-sad'></i></h1>
                    )}
            </ul>
            </div>
        </section>
        <Footer/>
        </>
    );
}

export default Home;
