import React, {useState} from 'react';
import './Login.css'
import {useHistory} from 'react-router-dom';


function Login({setName, setPhone, setEmail ,setPass,  handleLogin}) {


  const [disabled, setDisabled] = useState(true);


  const history = useHistory()


 
     
    return (
        <>
            <button className='goHome' onClick={() => history.push('/')}><i class='bx bx-chevron-left'></i>Go Home</button>

            <div className="form-box">
              <form> 
                <h1 className='login-title'><i class='bx bxs-user-circle'></i></h1> 
                 <div className=" box name-box">
                 <i className='bx bxs-user'></i>
                  <input required onChange={(e) => setName(e.target.value)} className='name' type="text" placeholder='Full Name' />
                 </div>
                 <div className=" box tel-box">
                 <i class='bx bxs-phone'></i>
                  <input required onChange={e => setPhone(e.target.value)} type="^(?:\d{10,12}|\+\d{10,12}|\w+@\w+\.\w{2,4})$" placeholder='Phone' />
                </div>
                <div className=" box email-box">
                  <i className='bx bxs-envelope'></i>
                  <input required  onChange={(e) => setEmail(e.target.value)} type="email" 
                 name="emailaddress"  placeholder='Email' />
                </div>
                <div className=" box pass-box">
                  <i className='bx bx-lock'></i>
                  <input required  onChange={e =>{if(e.target.value.length >= 3){setDisabled(false)} else{setDisabled(true)} setPass(e.target.value)}} type="password" placeholder='Password' />
                </div>
               <div className="btn-big-box">
               <div className="btn-boxss">
                <button onClick={handleLogin} disabled={disabled} className='login-btn'>Log In</button>
                <a href="#" className='forget__link'>Forget password?</a>
                </div>
                <div class="social__links">
                    <p>Or login with</p>
                    <i class='bx bxl-google-plus-circle'></i>
                    <i class='bx bxl-facebook-circle'></i>
                    <i class='bx bxl-twitter'></i>
                    <i class='bx bxl-instagram'></i>
                  </div>
               </div>
              </form>
            </div>
        </>
    );
}

export default Login;


