import React from 'react';
import Home from '../Home/Home'
import Card from '../Card/Card'
import Cart from '../Cart/Cart'
import Login from '../Login/Login'
import Public from '../../Backend/Tokens/Public'
import Private from '../../Backend/Tokens/Private'
import {Route, Switch} from 'react-router-dom';

const AllProps = ({productItems, cartItems, handleRemoveProduct , handleAddProduct, handleToken, handleLogin, setName, setPhone, setEmail, setPass, name, phone, email, pass, searchName, FilterProduct, search}) => {
    return (
        <Switch>
            <Route path='/' exact>
                <Home productItems={productItems} cartItems={cartItems} handleAddProduct={handleAddProduct}
                      name={name} phone={phone} email={email} handleToken={handleToken}
                     searchName={searchName} FilterProduct={FilterProduct} search={search}
                />
            </Route>
            <Route path='/card/:id' exact>
                <Card productItems={productItems} handleAddProduct={handleAddProduct} cartItems={cartItems}
                name={name} phone={phone} email={email} handleToken={handleToken}
                />
            </Route>
            <Private path='/cart' exact>
                <Cart handleAddProduct={handleAddProduct}  handleRemoveProduct={handleRemoveProduct}cartItems={cartItems} name={name} email={email} phone={phone} handleToken={handleToken}/>
            </Private>
            <Public path='/login' exact>
                <Login setName={setName} setPhone={setPhone} setEmail={setEmail} setPass={setPass} handleLogin={handleLogin} />
            </Public>
        </Switch>
    );
}

export default AllProps;
