import React from 'react';
import Header from '../Header/Header'
import Footer from '../Footer/Footer'
import {useHistory, useParams} from 'react-router-dom';
import './Card.css'

const Card = ({productItems,handleAddProduct, cartItems, name, email, handleToken, phone}) => {
  const history = useHistory();
  const {id} = useParams()
  const findProduct = productItems.find((item) => item.id == id)
    return (
        <>
          <Header cartItems={cartItems} name={name} phone={phone} email={email} handleToken={handleToken}/> 
          <section className='card'>
            <div className="container">
                <button className='goHomes' onClick={() => history.push('/')}><i class='bx bx-chevron-left'></i>Go Home</button>
                <ul className='card-list'>
                  <li key ={findProduct.id}>
                      <div className="items-img-box">
                          <img src={findProduct.image}  alt={findProduct.name} />
                      </div>
                      <div className="items-info-box">
                          <h2>{findProduct.title}</h2>
                          <p className="descitions">
                           Nike, originally known as Blue Ribbon Sports (BRS), was founded by <br /> University of Oregon track athlete Phil Knight and his coach, Bill Bowerman, <br /> on January 25, 1964.[12] The company initially operated in Eugene, Oregon as <br /> a distributor for Japanese shoe maker Onitsuka Tiger, making most sales at <br /> track meets out of Knight's automobile.[12]
                          </p>
                          <div className="btn-boxs">
                              <button className='btn-add' onClick={() =>{ handleAddProduct(findProduct)
                              alert('Product added to the cart')
                              }}>Add to Cart</button>
                              <p className='price'>{`$ ${findProduct.price}`}</p>
                        </div>
                     </div>
                  </li>
               </ul>
            </div>
        </section>
        <Footer />
        </>
    );
}

export default Card;
