import React from 'react';
import './Header.css'
import {Link} from 'react-router-dom'
import useToken from '../../Backend/Hooks/Hooks';
const Header = ({cartItems, handleToken, name, email, phone, searchName,FilterProduct}) => {
 const [token] = useToken()

    return (
          <header>
            <nav>
                <div className="container">
                  <div className="nav-box">
                  <h1 className='brand-name'>Nike-Shop</h1>
                  <form className='form'>
                      <input type="text" value={searchName} onChange={FilterProduct} placeholder='Search' />
                      <i class='bx bx-search'></i>
                  </form>
                  <div className="korzinka-box">
                   <div className="kor-box">
                       <Link className='kor-link' to={`/cart`}> <i className='bx bx-cart'></i>
                        <span className='spans'>{cartItems.length}</span>
                        </Link>
                   </div>
                    <div className="user-box">
                    <Link className='user-link' to='/login'><i className='bx bxs-user-circle'></i>
                    {token && <span className='user-span'>A</span>}
                    </Link>
                    {token && <div className="user-info-box">
                         <h1 className='user-profil'>Profil</h1>
                        <h3 className='user-name'> <span className='user-email'>FullName:</span>  {JSON.parse(window.localStorage.getItem('name'))}</h3>
                        <h3 className='user-email'> Phone:  {JSON.parse(window.localStorage.getItem('phone'))}</h3>
                        <h3 className='user-email'>Email:  {JSON.parse(window.localStorage.getItem('email'))}</h3>
                        <button className='log-out' onClick={handleToken} >Log Out</button>
                    </div>}
                    </div>
                  </div>
                  </div>

                </div>
            </nav>
        </header>
    );
}

export default Header;
