import React from 'react';
import {useHistory} from 'react-router-dom'
import './Cart.css'
import Header from '../Header/Header'

const Cart = ({cartItems, name, email, handleToken, phone, handleAddProduct, handleRemoveProduct}) => {
    const history = useHistory()
    const totalPrice = cartItems.reduce((price, item) => price + item.quantity * item.price, 0)

    return (
        <>
         <Header cartItems={cartItems} name={name} phone={phone} email={email} handleToken={handleToken}/>
         <section className='cart'>
            <div className="container">
             <div className="box">
             <button className='goHomess' onClick={() => history.push('/')}><i className='bx bx-chevron-left'></i>Go Home</button>
             <p className='total'>{`Cart Total: $ ${totalPrice.toFixed(2)}`}</p>
             </div>


               {cartItems.length === 0 && 
               <h1 className='nocart'>No products are added  
               <i className=' icons-box bx bx-meh'></i>
               <i className=' icons-box bx bx-sad'></i>
               <i className=' icons-box bx bxs-info-circle' ></i>
               </h1>
               }
               <ul className='item-list'>
                   {cartItems.map((item) =>(
                       
                       <li key ={item.id}>
                        
                           <div className="item-img-box">
                            <img src={item.image} width='468' height='270' alt={item.title} />
                           </div>
                          <div className="item-info-box">
                            <h2>{item.title}</h2>
                                <div className="item-big-btn">
                                    <div className="item-btn-box">
                                        <button onClick={() => handleRemoveProduct(item)} className="minus"><i class='bx bxs-trash'></i></button>
                                    </div>
                                    <h4>${item.quantity * item.price}</h4>
                                </div>
                          </div>
                       </li>
                   ))}
               </ul>
               <div className="shopping-box">
                    <button onClick={ () =>{
                        if(cartItems.length !== 0){
                            alert(`Xaridingiz uchun raxmat. Umumiy xarajat $ ${totalPrice.toFixed(2)}`)
                        }
                    } } className='shopping'>Shopping</button>
               </div>
              
            </div>
        </section>
        </>
    );
}

export default Cart;
